<template>
	<div id="order-refund">
		<public-header title="部分退款"></public-header>
		<div class="content">
			<div class="tips">退款商品</div>
			<div class="refund-goods">
				<div class="flex-lr padding-10-b padding-10-lr">
					<div class="flex">
						<div class="avatar">
							<img :src="store.logo" alt="">
						</div>
						<div class="font-14 padding-10-l">{{store.title}}</div>
					</div>
					<div class="store-sign ellipsis">{{store.delivery_title}}</div>
				</div>
				<div class="goods-list" v-if="order.goods">
					<div class="goods flex" v-for="item in order.goods">
						<img class="goods-img" :src="item.thumb" alt="">
						<div class="goods-info">
							<div class="ellipsis font-16">{{item.goods_title}}</div>
							<div class="flex-lr">
								<div class="number">x{{item.can_refund_num}}</div>
								<div class="handle">
									<div class="count c-warning" v-if="item.can_refund_num == 0">已退款</div>
									<template v-else>
										<div class="icon icon-minus" v-if="item.can_refund_num > 0" @click="onCalculate(item.id, '-')"></div>
										<div class="count" v-if="refund_data && refund_data.refund_part && refund_data.refund_part[item.id]">{{refund_data.refund_part[item.id].total_num}}</div>
										<div class="count" v-else>0</div>
										<div class="icon icon-plus" @click="onCalculate(item.id, '+')"></div>
									</template>
								</div>
							</div>
						</div>
					</div>
				</div>
				<div class="padding-10">
					<div class="flex-lr font-14">
						<span>配送费</span>
						<span class="delivery-price">¥{{order.delivery_fee}}</span>
					</div>
					<div class="explain">部分退款，配送费不退回</div>
				</div>
				<div class="padding-10" v-if="order.extra_fee > 0">
					<div class="flex-lr font-14">
						<span>附加费</span>
						<span class="delivery-price">¥{{order.extra_fee}}</span>
					</div>
					<div class="explain">部分退款，附加费不退回</div>
				</div>
				<div class="padding-10-t padding-10-lr">
					<div class="flex-lr">
						<div class="refund-fee">退款金额 <span class="font-14">（包含餐盒费）</span></div>
						<div class="font-16 c-danger">￥{{refund_data.refund_total_fee}}</div>
					</div>
					<div class="refund-explain">
						备注：单独商品的退款金额是在刨除配送费后按照优惠活动等比计算得出。<span class="hide">如您选择全部商品则按照全部退款返还您支付的全部金额</span>
						<br>
						<span class="c-danger">
							退款金额 = [(退款菜品现价+餐盒费） / （全部菜品现价总和+餐盒费)]  ×（最终支付价格 - 配送费 - 附加费）
						</span>
					</div>
				</div>
			</div>
			<div class="tips">请选择退款原因</div>
			<van-radio-group v-model="reasonSelect" class="refund-list">
				<van-cell-group>
					<template v-for="(item, index) in reasons">
						<van-cell>
							<van-radio :name="index">{{item}}</van-radio>
						</van-cell>
					</template>
				</van-cell-group>
			</van-radio-group>
			<div class="refund-btn" @click="onSubmit">确认退款{{refund_data.refund_total_fee > 0 ? '￥' + refund_data.refund_total_fee : ''}}</div>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData, setData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader
		},
		data() {
			return {
				showPreLoading: true,
				reasons: {},
				store: {},
				order: {},
				reasonSelect: 'foodSoldOut',
				refund_data: {
					refund_total_fee: 0,
				},
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/order/takeout/refund',
					data: {
						id: that.order_id
					},
					success: function(res) {
						that.reasons = res.reasons;
						that.store = res.store;
						that.order = res.order;
					}
				});
			},
			onCalculate(stat_id, sign) {
				var that = this;
				setData({
					vue: that,
					url: 'manage/order/takeout/refund_calculate',
					data: {
						id: that.order_id,
						stat_id: stat_id,
						sign: sign,
						refund_data: that.refund_data
					},
					success: function(res) {
						that.refund_data = res.refund_data;
					}
				});
			},
			onSubmit() {
				var that = this;
				if(that.refund_data.refund_total_fee == 0) {
					that.util.$toast('请选择退货商品', '', 1000);
					return;
				}
				if(!that.reasonSelect) {
					that.util.$toast('请选择原因', '', 1000);
					return;
				}
				setData({
					confirm: '确认发起部分退款吗',
					vue: that,
					url: 'manage/order/takeout/refund',
					data: {
						id: that.order_id,
						reason: that.reasonSelect,
						refund_data: that.refund_data
					},
					success: function(res) {
						that.util.$toast(res, that.util.getUrl({path: 'pages/order/index', query: {status: that.order.status}}), 1000);
					}
				});
			},
		},
		created(){
			this.order_id = this.$route.query.id || this.$route.query.order_id;
		},
		mounted(){
			this.onLoad();
		},
	}
</script>

<style>
	#order-refund .tips{
		padding: 10px;
		font-size: 14px;
		color: #666;
	}
	#order-refund .refund-goods{
		padding: 10px 0;
		margin: 0 10px;
		background-color: #fff;
	}
	#order-refund .refund-goods .avatar{
		width: 30px;
		height: 30px;
	}
	#order-refund .refund-goods .avatar img{
		width: 100%;
		height: 100%;
		border-radius: 100%;
	}
	#order-refund .refund-goods .store-sign{
		font-size: 14px;
		background-color: #ff2d4b;
		color: #fff;
		text-align: center;
		border-radius: 8px 0 8px 0;
		max-width: 30%;
		padding: 3px 6px;
	}
	#order-refund .refund-goods .goods-list .goods:first-child{
		padding-top: 10px;
	}
	#order-refund .refund-goods .goods{
		background-color: #f8f8f8;
		padding: 0 10px 10px;
	}
	#order-refund .refund-goods .goods .goods-img{
		width: 70px;
		height: 70px;
	}
	#order-refund .refund-goods .goods .goods-info{
		height: 70px;
		display: flex;
		flex-flow: column nowrap;
		justify-content: space-between;
		flex: 1;
		padding-left: 10px;
	}
	#order-refund .refund-goods .goods .goods-info .number{
		color: #9C9C9C;
		align-self: flex-end;
	}
	#order-refund .refund-goods .goods .goods-info .handle{
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		align-items: center;
	}
	#order-refund .refund-goods .goods .goods-info .handle .icon{
		padding: 5px;
		border-radius: 100%;
		background-color: #fff;
		color: #B0B0B0;
		border: 1px solid #ccc;
	}
	#order-refund .refund-goods .goods .goods-info .handle .icon-plus{
		background-color: #33aafc;
		border-color: #33aafc;
		color: #fff;
	}
	#order-refund .refund-goods .goods .goods-info .handle .count{
		padding: 0 10px;
	}
	#order-refund .refund-goods .delivery-price{
		text-decoration:line-through
	}
	#order-refund .refund-goods .explain{
		text-align: right;
		color: #999;
		font-size: 14px;
		padding-top: 5px;
	}
	#order-refund .refund-goods .refund-fee{
		font-size: 16px;
		font-weight: bold;
		padding-right: 5px;
	}
	#order-refund .refund-goods .refund-explain{
		color: #999;
		font-size: 14px;
		margin-top: 5px;
		line-height: 1.2;
	}
	#order-refund .refund-list{
		margin: 0 10px;
		padding-bottom: 60px;
	}
	#order-refund .refund-btn{
		position: fixed;
		left: 0;
		bottom: 0;
		right: 0;
		height: 50px;
		line-height: 50px;
		text-align: center;
		color: #fff;
		background-color: #33aafc;
	}
</style>


